





























































import Vue from 'vue'
import Modal from '@/components/Modal.vue'
import { mapState, mapActions, mapGetters } from 'vuex'

export default Vue.extend({
  name: 'Login',
  metaInfo: {
    title: 'Login'
  },
  components: { Modal },
  data () {
    return {
      username: '',
      password: '',
      authError: false
    }
  },
  computed: {
    ...mapState([
      'credentials'
    ]),
    ...mapGetters([
      'isAuthenticated'
    ])
  },
  directives: {
    uppercase: {
      update (el) {
        (el as HTMLInputElement).value = (el as HTMLInputElement).value.toUpperCase()
      }
    }
  },
  methods: {
    ...mapActions([
      'authenticate'
    ]),
    async login () {
      if (this.username.includes('@afp.com')) {
        this.$toasted.show(this.$t('auth.warning-email').toString(), {
          position: 'bottom-center',
          duration: 1500,
          type: 'error'
        })
        this.authError = true
        return false
      }
      try {
        await this.authenticate({ username: this.username, password: this.password })
        this.$toasted.show(this.$t('auth.success.title').toString(), {
          position: 'bottom-center',
          duration: 1500,
          type: 'success'
        })
        this.authError = false
        this.$ga.enable()
        const redirects = this.$route.query.redirect
        if (redirects) {
          if (Array.isArray(redirects)) {
            const redirect = redirects[0]
            if (redirect) {
              this.$router.push({ path: redirect })
            }
          } else {
            this.$router.push({ path: redirects })
          }
        } else {
          this.$router.push({ name: 'deck' })
        }
      } catch (e) {
        this.authError = true
      }
    },
    close () {
      this.$ga.enable()
      this.$router.push({ name: 'deck' })
    }
  }
})
